@import "src/variables/variables";


.slideShow {
  width: 600px;
  height: 200px;
  position: relative;
    .image {
      width: 100%;
      height: 100%;
      position: absolute;
    }
}
.active {
  background: #000;
  opacity: 0;

}
.none {
  background: #000;
  opacity: 0; visibility: 0;
}